.form_modal_cheats {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding-bottom: 100px;
  margin-top: 20px;
}
/* .MuiDialog-paperWidthSm{
  width: 70%;
  max-width: 1200px;
  padding-bottom: 10px;
  padding-right: 10px;
} */

.desc_input {
  margin-top: 5px;
}

.form_left {
  width: 40%;
  height: 100%;
}
.form_right {
  display: flex;
  flex-direction: column;
  width: 55%;
}

.uploadFileDiv {
  margin-top: 70px;
  display: flex;
  flex-direction: column;
  width: 100%;
  justify-content: center;
  align-items: center;
}
.uploadFileDiv button {
  margin-top: 10px;
}

.image_category {
  position: absolute;
  right: 55%;
  top: 10px;
  width: 50px;
}
.Title {
  display: flex;
  align-items: center;
  height: 100%;
}

.image_cheat {
  max-width: 300px;
  height: 240px;
  margin-top: -50px;
  margin-bottom: 10px;
  overflow: hidden;
  border-radius: 20px;
}

.image_cheat img {
  width: 100%;
  height: 100%;
  transition: all 1s;
}
.divider_downloads {
  margin-top: 15px;
  margin-bottom: 15px;
}
.downloadDiv {
  width: 100%;
  display: flex;
  justify-content: space-between;
}
.downloadTitleInput {
  margin-right: 20px;
}

@media (max-width: 1200px) {
  .form_modal_cheats {
    flex-direction: column;
  }
  .MuiDialog-paperWidthXl {
    width: 100vw;
  }
  .MuiDialog-paper {
    margin: 5px;
  }
  .form_left {
    width: 100%;
  }
  .form_right {
    width: 100%;
  }
}
