/* NAVBAR */
.navbar a {
  color: inherit;
  font-family: inherit;
  font-size: inherit;
  text-decoration: none;
}

.navbar {
  background: transparent;
  color: rgb(13, 26, 38);
  position: fixed;
  top: 0;
  height: 60px;
  line-height: 60px;
  width: 100vw;
  z-index: 10;
  transition: 0.5s;
  margin-bottom: 50px;
}
.solid {
  background-color: white;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2);
}

.nav-wrapper {
  margin: auto;
  text-align: center;
  width: 70%;
}

.navbar .logoNavbar {
  float: left;
  margin-left: 28px;
  font-size: 1.5em;
  height: 60px;
  letter-spacing: 1px;
  text-transform: uppercase;
}

.navbar ul {
  display: inline-block;
  float: right;
  list-style: none;
  margin-top: -2px;
  text-align: right;
  transition: -webkit-transform 0.5s ease-out;
  transition: transform 0.5s ease-out;
  transition: transform 0.5s ease-out, -webkit-transform 0.5s ease-out;
  -webkit-transition: transform 0.5s ease-out;
}

.navbar li {
  display: inline-block;
}

.navbar li a {
  color: rgb(13, 26, 38);
  display: block;
  font-size: 1.2em;
  height: 50px;
  letter-spacing: 1px;
  margin: 0 20px;
  padding: 0 4px;
  position: relative;
  text-decoration: none;
  text-transform: uppercase;
  transition: all 0.5s ease;
  -webkit-transition: all 0.5s ease;
  font-weight: 500;
}

.navbar li a:hover {
  color: rgb(36, 36, 36);
  transition: all 1s ease;
  -webkit-transition: all 1s ease;
}

/* Bottom Line */
.navbar li a:before {
  left: 0;
  transition: 0.5s;
}
.navbar li a:after {
  background: rgb(36, 36, 36);
  right: 0;
}

.navbar li a:before,
.navbar li a:after {
  content: "";
  position: absolute;
  width: 0%;
  height: 1px;
  bottom: -1px;
  background: rgb(36, 36, 36);
}
.navbar li a:hover:before {
  background: rgb(36, 36, 36);
  width: 100%;
  transition: width 0.5s cubic-bezier((0.22, 0.61, 0.36, 1));
}

.navbar li a:hover:after {
  background: transparent;
  width: 100%;
}

.navbar li a.active:before {
  background: rgb(36, 36, 36);
  width: 100%;
  transition: width 0.5s cubic-bezier((0.22, 0.61, 0.36, 1));
}

.navbar li a.active:after {
  background: transparent;
  width: 100%;
}

/* NAVBAR COLOR SOLIDO */
.navbar.solid li a:hover {
  color: var(--color_principal_claro_solido);
}

.navbar.solid li a.active {
  color: var(--color_principal_claro_solido);
}

.navbar.solid li a:after {
  background: var(--color_principal_claro_solido);
}

.navbar.solid li a:before,
.navbar.solid li a:after {
  background: var(--color_principal_claro_solido);
  content: "";
  position: absolute;
  width: 0%;
  height: 1px;
  bottom: -1px;
}
.navbar.solid li a:hover:before {
  background: var(--color_principal_claro_solido);
  width: 100%;
  transition: width 0.5s cubic-bezier((0.22, 0.61, 0.36, 1));
}

.navbar.solid li a.active:before {
  background: var(--color_principal_claro_solido);
  width: 100%;
  transition: width 0.5s cubic-bezier((0.22, 0.61, 0.36, 1));
}

