.footer{
  position: absolute;
  bottom: 0;
  margin-top: 50px;
  padding: 5px 0px 5px 0px;
  box-sizing: border-box;
  background-color: var(--color_principal);
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  -webkit-box-shadow: 0px -6px 26px -10px rgba(0,0,0,0.52); 
  box-shadow: 0px -6px 26px -10px rgba(0,0,0,0.52);
}
.footer a {
  text-decoration: none;
  color: var(--color_principal_oscuro);
}
.credits{
  font-size: 15px;
}

/* RESPONSIVE */
@media (max-width: 1024px) {
  .footer{
    flex-direction: column;
  }
  .footer p{
    font-size: 12px;
  }
  .credits{
    font-size: 10px !important;
  }
}